.logo {
  display: flex;
  &__img {
    max-width: 90px;
  }
  &__text {
    font-size: 10px;
    line-height: 12px;
    max-width: 200px;
    border-left: 1px solid #000000;
    padding-left: 10px;
    margin-left: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}



