.quiz {
  background: rgba(175, 177, 185, 0.8);
  position: fixed;
  z-index: 10;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: none;
  &.shown {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__close {
    position: absolute;
    width: 18px;
    height: 18px;
    background: #FFFFFF;
    border-radius: 50%;
    right: 11px;
    top: 8px;
    cursor: pointer;
    &::before, &::after {
      content: '';
      display: block;
      border-radius: 2px;
      background: #C4C4C4;
      width: 2px;
      height: 10px;
      position: absolute;
      left: 50%;
      top: 50%;
    }
    &::before {
      transform: translate(-50%, -50%) rotate(45deg);
    }
    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }
  &__inner {
    background: #FFFFFF;
    border-radius: 20px;
    position: absolute;
    z-index: 2;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    min-height: 200px;
    min-width: 200px;
    display: flex;
    min-height: 580px;
    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      top: 16px;
      bottom: 18px;
      left: -18px;
      right: -16px;
      background: rgba(255, 255, 255, 0.6);
      border-radius: 20px;
    }
    &.autoheight {
      position: relative;
      min-height: auto;
      height: auto;
      bottom: auto;
      transform: none;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: auto;
    }
  }
  &__main {
    padding: 28px 80px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-width: 600px;
    flex-grow: 1;
    &.hidden {
      display: none;
    }
  }
  &__sidebar {
    background: #F4F6FA;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 225px;
    // max-width: 225px;
    // width: 100%;
    padding: 18px 16px;
    border-radius: 0 20px 20px 0; //FIXME сделать иначе
    &.hidden {
      display: none;
    }
    &::before {
      content: '';
      display: block;
      background: #F4F6FA;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
      height: 37px;
      width: 37px;
    }
  }
  &__header {
    padding-left: 18px;
    padding-right: 18px;
    margin-bottom: 50px;
  }
  &__intro {
    font-size: 10px;
    margin-bottom: 18px;
  }
  &__progress {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__progress-item {
    background: #E5E5E5;
    border-radius: 2px;
    width: 17%;
    height: 3px;
    &.checked {
      background: $color-accent;
    }
  }
  &__content-wrapper {
    flex-grow: 1;
    position: relative;
  }
  &__content {
    &.hidden {
      display: none;
      opacity: 0;
    }
  }
  &__title {
    font-family: $ff-secondary;
    font-weight: 600;
    font-size: 17px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 20px;
    &.not-allow {
      animation: notAllow linear .35s 2;
    }
    @keyframes notAllow {
      0% {transform: translateX(0px);}
      25% {transform: translateX(-10px);}
      50% {transform: translateX(0px);}
      75% {transform: translateX(10px);}
      100% {transform: translateX(0px);}
    }
  }
  &__subtitle {
    font-size: 12px;
    text-transform: lowercase;
    text-align: center;
    margin-bottom: 40px;  //FIXME ?????????????????????????
  }
  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__btn {
    font-family: $ff-secondary;
    font-weight: 600;
    font-size: 12px;
    text-transform: uppercase;
    padding: 10px 30px 10px 35px;
    border-radius: 50px;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    border: 1px solid transparent;
    &:focus {
      outline: none;
      border: 1px solid rgb(223, 128, 5);
    }
    &[disabled] {
      visibility: hidden;
    }
    &::after {
      content: '';
      display: block;
      background: no-repeat center / contain;
      width: 14px;
      height: 10px;
    }
    &--prev {
      background: #E5E5E5;
    }
    &--next {
      background: $color-accent;
    }
    &--prev::after {
      background-image: url(../img/icon/arrow-left.svg);
      order: -1;
      margin-right: 10px;
    }
    &--next::after {
      background-image: url(../img/icon/arrow-right.svg);
      margin-left: 10px;
    }
  }
  &__logo {
    margin-bottom: 15px;
  }
  &__director {
    // margin-bottom: 25px;
  }
  &__director-img {
    max-width: 115px;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 10px;
    border-radius: 50%;
    overflow: hidden;
  }
  &__director-title {
    font-weight: 500;
    font-family: $ff-secondary;
    margin-bottom: 5px;
  }
  &__director-subtitle {
    font-weight: 300;
    font-size: 10px;
    margin-bottom: 25px;
  }
  &__tooltip {
    padding: 18px 25px;
    font-size: 10px;
    line-height: 12px;
    text-align: center;
    background-color: #ffffff;
    position: relative;
    z-index: 2;
    &::before {
      content: '';
      display: block;
      background: #ffffff;
      position: absolute;
      z-index: -1;
      left: 20px;
      top: 0;
      transform: translateY(-50%) rotate(45deg);
      height: 20px;
      width: 20px;
    }
    &::after {
      content: '';
      display: none;
      position: absolute;
      right: 5px;
      top: 16px;
      width: 10px;
      height: 6px;
      background: no-repeat center / contain;
      background-image: url(../img/icon/dropdown.svg);
    }
    &.closed::after {
      display: block;
    }
    &.opened::after {
      display: block;
      transform: rotate(180deg);
    }

  }
  &__close {}
  &__rooms {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__rooms-item {
    cursor: pointer;
    flex-basis: calc(50% - 26px);
    border-radius: 5px;
    box-shadow: 0px 4px 10px rgba(27, 82, 121, 0.1);
    margin-bottom: 36px;
    border: 1px solid transparent;
    transition: .15s;
    position: relative;
    background: no-repeat center / cover;
    min-height: 150px;
    // flex-grow: 1;
    &.checked {
      border: 1px solid #72A4C7;
      box-shadow: 0px 4px 15px rgba(0, 64, 138, 0.2);
    }
    &:hover {
      border: 1px solid #72A4C7;
      box-shadow: 0px 4px 15px rgba(0, 64, 138, 0.2);
    }
  }
  &__rooms-img {
    display: none;
  }
  &__rooms-cb {
    position: relative;
    z-index: -1;
    opacity: 0;
    cursor: pointer;
  }
  &__rooms-title {
    position: absolute;
    left: 12px;
    bottom: 10px;
    font-size: 12px;
  }
  &__area {
    margin-top: 100px;
  }
  &__area-range {
    margin-bottom: 15px;
    & .noUi-tooltip::after {
      content: ' м²';
    }
  }
  &__area-minmax {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    margin-bottom: 20px;
  }
  &__area-min::after, &__area-max::after {
    content: ' м²';
  }
  &__area-input {
    max-width: 200px;
    & > input {
      width: 100%;
      background: #FFFFFF;
      border-radius: 5px;
      text-align: center;
      padding: 10px;
      max-width: none;
      width: 100%;
      color: rgba(0, 0, 0, 0.4);
      transition: 0.15s;
      border: 1px solid rgba(0, 0, 0, 0.4);
      &:focus {
        color: #000000;
        outline: none;
        // box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.35);
        box-shadow: 0px 4px 10px rgba(27, 82, 121, 0.1);
        border: 1px solid #72A4C7;
      }
    }
  }
  &__region {}
  &__region-list, &__renovated-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__region-item, &__renovated-item {
    display: flex;
    align-items: center;
    padding: 12px 10px;
    flex-basis: calc(50% - 16px);
    font-size: 14px;
    cursor: pointer;
    border-radius: 5px;
    box-shadow: 0px 4px 10px rgba(27, 82, 121, 0.1);
    margin-bottom: 18px;
    border: 1px solid transparent;
    transition: .15s;
    position: relative;
    flex-grow: 0;
    flex-shrink: 1;
    &.checked {
      border: 1px solid #72A4C7;
      box-shadow: 0px 4px 15px rgba(0, 64, 138, 0.2);
    }
    &.checked .quiz__region-label::after,
    &.checked .quiz__renovated-label::after {
      display: block;
    }
    &:hover {
      border: 1px solid #72A4C7;
      box-shadow: 0px 4px 15px rgba(0, 64, 138, 0.2);
    }
  }
  &__region-radio, &__renovated-radio {
    padding: 0;
    margin: 0;
    position: absolute;
    z-index: -1;
    opacity: 0;
  }
  &__region-label, &__renovated-label {
    position: relative;
    width: 12px;
    height: 12px;
    margin-right: 10px;
    cursor: pointer;
    &::before, &::after {
      cursor: pointer;
      content: '';
      position: absolute;
      display: block;
      border-radius: 3px;
    }
    &::before {
      width: 12px;
      height: 12px;
      background: #E5E5E5;
      left: 0;
      top: 0;
    }
    &::after {
      width: 6px;
      height: 6px;
      background: #999999;
      border-radius: 1.5px;
      left: 3px;
      top: 3px;
      display: none;
    }
  }
  &__region-title, &__renovated-title {}
  &__renovated-list {
    flex-direction: column;
    align-items: flex-start;
  }
  &__renovated-item {
    flex-basis: fill;
    max-width: 140px;
    width: 100%;
  }
  &__floor {
    margin-top: 100px;
  }
  &__floor-range {
    margin-bottom: 15px;
    & .noUi-tooltip::after {
      content: ' этаж';
    }
  }
  &__floor-minmax {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    margin-bottom: 20px;
  }
  &__floor-min::after, 
  &__floor-max::after {
      content: ' этаж';
  }
  &__floor-input {
    max-width: 200px;
    & > input {
      width: 100%;
      background: #FFFFFF;
      border-radius: 5px;
      text-align: center;
      padding: 10px;
      max-width: none;
      width: 100%;
      color: rgba(0, 0, 0, 0.4);
      transition: 0.15s;
      border: 1px solid rgba(0, 0, 0, 0.4);
      &:focus {
        color: #000000;
        outline: none;
        // box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.35);
        box-shadow: 0px 4px 10px rgba(27, 82, 121, 0.1);
        border: 1px solid #72A4C7;
      }
    }
  }
  &__confirm {
    display: flex;
    flex-direction: column;
    // align-items: center;
    padding: 22px;
    min-width: 800px;
    &.hidden {
      display: none;
    }
  }
  &__confirm-logo {
    margin-bottom: 60px;
    // align-self: stretch;
    & .logo__img {
      max-width: 160px;
      width: 100%;
      margin: 0 auto;
    }
  }
  &__confirm-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 255px;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 30px;
  }
  &__confirm-label {
    font-size: 12px;
    margin-bottom: 5px;
  }
  &__confirm-input {
    margin-bottom: 54px;
    background: #FFFFFF;
    border-radius: 5px;
    text-align: center;
    padding: 10px;
    max-width: none;
    width: 100%;
    color: rgba(0, 0, 0, 0.4);
    transition: 0.15s;
    border: 1px solid rgba(0, 0, 0, 0.4);
    &:focus {
      color: #000000;
      outline: none;
      // box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.35);
      box-shadow: 0px 4px 10px rgba(27, 82, 121, 0.1);
      border: 1px solid #72A4C7;
    }
  }
  &__confirm-btn {
    padding: 10px 42px;
    border: none;
    background: #FFC700;
    box-shadow: 0px 4px 20px 0px rgba(255, 122, 0, 0.4);
    border-radius: 50px;
    width: 100%;
    font-family: $ff-secondary;
    font-weight: 600;
    font-size: 17px;
    line-height: 22px;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    margin-bottom: 15px;
    transition: .15s;
    border: 1px solid transparent;
    &:focus {
      outline: none;
      border: 1px solid rgb(223, 128, 5);
    }
    &:hover {
      box-shadow: 0px 1px 8px 0px rgba(255, 122, 0, 0.3);
    }
    &:active {
      transform: translateY(2px);
      background: #e2b205;
    }
    
  }
  &__confirm-policy {
    font-size: 8px;
    line-height: 10px;
    text-align: center;
    color: #9C9C9C;
    max-width: 160px;
  }
  &__success {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 20px 70px;
    &.hidden {
      display: none;
    }
    & .quiz__title {
      line-height: 30px;
    }
  }
  &__success-logo {
    margin-bottom: 25px;
    & .logo__img {
      max-width: 115px;
    }
  }
}





.noUi-target {
  height: 5px;
  background-color: #E5E5E5;
  &::before {
    content: '';
    background-color: $color-accent;
    display: block;
    position: absolute;
    width: 12px;
    height: 12px;
    left: -6px;
    top: -4px;
    border-radius: 3px;
  }
  &::after {
    // content: attr(data-minimum)' м²';
    position: absolute;
    left: -6px;
    bottom: -24px;
    font-size: 12px;
    border-radius: 3px;
  }
}
.noUi-connect {
  background-color: $color-accent;
}
.noUi-target {
  border: none;
}
.noUi-handle {
  background-color: $color-accent;
  border-radius: 3px;
  box-shadow: none;
  border: 1px solid transparent;
  &::before, &::after {
    display: none;
  }
  &:focus {
    outline: none;
    border: 1px solid rgb(223, 128, 5);
  }
}
.noUi-touch-area {
  &:focus {
    outline: none;
  }
}
.noUi-tooltip {
  font-size: 12px;
  padding: 7px;
  border: 1px solid #E5E5E5;
  border-radius: 3px;
}
.noUi-horizontal .noUi-handle {
  width: 12px;
  height: 12px;
  right: -6px;
  top: -4px;
  cursor: pointer;
}
.noUi-connects {}
.noUi-base {}



