.header {
  padding: 8px 64px 8px 32px;
  position: absolute;
  z-index: 5;
  width: 100%;
  left: 0;
  top: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    & .logo__img {
      // max-width: 90px;
    }
  }
  &__contacts {
    display: flex;
    align-items: center;
  }
  &__geo {
    max-width: 120px;
    margin-right: 60px;
    font-size: 10px;
    line-height: 12px;
    position: relative;
    &::before {
      content: '';
      display: block;
      position: absolute;
      left: -30px;
      top: 0;
      width: 15px;
      height: 17px;
      background: no-repeat center / contain;
      background-image: url(../img/icon/geo.svg);
    }
  }
  &__tel {
    font-size: 12px;
    font-weight: 700;
    border-bottom: 2px dotted #000000;
    display: inline-block;
    &:hover {
      border-bottom-style: solid;
    }
  }
}





@media (min-width: 1900px) {
  .header {
    &__logo {
      .logo__img {
        max-width: 160px;
        width: 100%;
      }
      .logo__text {
        font-size: 16px;
        max-width: 380px;
        line-height: 18px;
      }
    }
    &__geo {
      font-size: 16px;
      max-width: 200px;
    }
    &__tel {
      font-size: 16px;
    }
  }
}
