.footer {
  padding: 28px 32px;
  position: absolute;
  z-index: 2;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__box {
    font-size: 10px;
    color: #E5E5E5;
    &:first-of-type {
      color: #9C9C9C;
    }
    & > a {
      text-decoration: underline;
    }
  }
}


